<style scoped>
.layout{
    height: 100%;
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}
.layout-container {
    height: 100%;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    float: right;
    margin: 0 auto;
    margin-right: 0;
}
.userinfo {
    color: #fff;
}
</style>
<template>
    <div class="layout">
        <Layout class="layout-container">
            <Header>
                <Menu mode="horizontal" theme="dark" active-name="1">
                    <div class="layout-logo"></div>
                    <div class="layout-nav">
                       <Dropdown placement="bottom-end" :visible="visible" style="margin-left: 20px">
                        <a href="javascript:void(0)" class="userinfo" @click="handleOpen">
                            <Avatar icon="ios-person" size="large" />
                            {{user.email}}
                            <Icon type="ios-arrow-down"></Icon>
                        </a>
                        <DropdownMenu slot="list" style="width: 300px">
                            <p></p>
                        </DropdownMenu>
                    </Dropdown>
                    </div>
                </Menu>
            </Header>
            <Layout>
                <Sider hide-trigger :style="{background: '#fff'}">
                    <Menu active-name="1-1" :style="{minHeight: '100%'}" theme="light" width="auto" :open-names="['1']">
                        <Submenu name="1">
                            <template slot="title">
                                <Icon type="md-laptop" />
                                IDC
                            </template>
                            <MenuItem name="1-1"></MenuItem>
                            <MenuItem name="1-2"></MenuItem>
                            <MenuItem name="1-3"></MenuItem>
                        </Submenu>
                    </Menu>
                </Sider>
                <Layout :style="{padding: '0 24px 24px'}">
                    <Content :style="{padding: '24px', minHeight: '280px', background: '#fff'}">
                        <router-view></router-view>
                    </Content>
                </Layout>
            </Layout>
        </Layout>
    </div>
</template>
<script>
    import axios from "axios";
    export default {
        data () {
            return {
                visible: false,
                user: {}
            }
        },
        methods: {
            handleOpen () {
                this.visible = true;
            },
            handleClose () {
                this.visible = false;
            }
        },
        mounted() {
             axios.get("http://"+location.hostname+"/home/user/getInfo").then(data => {
                if(data.data.code==1) {
                    this.user = data.data.data;
                } else {
                    console.error(data.data.msg);
                }
             });
        }
    }
</script>